import {iosVhFix} from './utils/ios-vh-fix';
import {initModals} from './modules/modals/init-modals';
import {initSwiper, initSwiperReviews} from './initSwiper.js';
import {cardFocus} from './subscriptions-focus.js';
import {initScroll} from './scroll.js';
import {phoneValidate} from './validation.js';
import {initTab} from './tabs.js';
import './video.js';

// ---------------------------------

window.addEventListener('DOMContentLoaded', () => {

  initSwiper;
  initSwiperReviews;
  initTab();

  // Utils
  // ---------------------------------

  iosVhFix();
  initScroll();

  // Modules
  // ---------------------------------

  //       'DOMContentLoaded',     'load'
  //  load   ,      
  window.addEventListener('load', () => {
    initModals();
    cardFocus();
    phoneValidate();
  });
});

